.main-registerform {
    position: fixed;
    top: 10%;  /* Center vertically a bit more for better mobile experience */
    left: 50%; /* Center horizontally */
    transform: translate(-50%, 0); /* Move to center horizontally */
    color: white;
    width: 90%; /* Full width for smaller screens */
    max-width: 25em; /* Limit max width for larger screens */
    height: auto;
    padding: 1.5em;
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    background: rgba(0, 0, 0, 0.723);
    backdrop-filter: blur(30px);
    z-index: 1001;
  
    @media (min-width: 768px) {
      width: 60%; /* Slightly narrower on medium screens */
      padding: 2em;
    }
  
    @media (min-width: 1024px) {
      top: 10%; /* Adjust top positioning */
      width: 25em; /* Set width for larger screens */
      padding: 1.5em;
    }
  
    // Style adjustments for mobile view (below 480px)
    @media (max-width: 480px) {
      width: 95%; /* Nearly full width on very small screens */
      padding: 1em;
      top: 5%; /* Adjust top to make sure it stays within view */
    }
  
    .input-field {
      display: flex;
      flex-direction: column;
      margin-bottom: 1rem;
  
      label {
        font-weight: bold;
        margin-bottom: 0.5rem;
      }
  
      input[type="text"],
      input[type="email"],
      input[type="password"],
      input[type="file"] {
        padding: 0.8em;
        border: 1px solid #ccc;
        border-radius: 5px;
        font-size: 1rem;
      }
  
      input[type="file"] {
        padding: 0.5rem; /* Make the file input a bit smaller */
      }
    }
  
    .submit-button {
      padding: 0.8em 1.2em;
      background-color: var(--secondary-color);
      color: white;
      border: none;
      border-radius: 5px;
      font-weight: bold;
      cursor: pointer;
      transition: background 0.3s;
  
      &:hover {
        background-color: var(--primary-color);
      }
    }
  }
  

.app__registerForm-submitButton {
    width: 100%;
    height: 50px;
    border: none;
    outline: none;
    border-radius: 50px;
    margin-top: 15px;
    cursor: pointer;
    box-shadow: 0 0 5px black;
    font-size: 18px;
    font-weight: 700;
    color: black;
    left: 10%;
  }
